/* CSS Document

Author: Lucas Mathis
*/


* {
  margin: 0;
  font-family: 'Maven Pro', sans-serif;
  color: #999;
  border-radius: 0;
}

/* Main Div Style */
#main {
  max-width: 940px;
  margin: 0 auto;
  background-color: #fff;
}

/* Navigation Style */
nav {
  max-width: 940px;
  min-width: 275px;
  margin: 0 auto;
  font-size: 84%;
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%);
  border: 0px solid #fff;
}

#navList{
  padding: 0;
  background: #999;
  font-size: 98%;
  margin: 0;
}
#navList li a:hover {
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%);
}
#navList li {
  margin: 0;
  padding: 0;
  color: #fff;
  border-bottom-style: solid;
  border-color: #000;
  border-width: 1px;

}
#navList li a {
  color: #fff;
}

.dropdown ul {
  background-color: #ccc;
}
.dropdown ul li:hover {
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%);
}

/* Fancy Smancy Divider line between images & text */
#indexDivider, hr {
  border: 0;
  height: 2px;
  background-image: -webkit-linear-gradient(left, rgba(204,204,204,0), rgba(204,204,204,0.9), rgba(204,204,204,0));
  background-image:    -moz-linear-gradient(left, rgba(204,204,204,0), rgba(204,204,204,0.9), rgba(204,204,204,0));
  background-image:     -ms-linear-gradient(left, rgba(204,204,204,0), rgba(204,204,204,0.9), rgba(204,204,204,0));
  background-image:      -o-linear-gradient(left, rgba(204,204,204,0), rgba(204,204,204,0.9), rgba(204,204,204,0));
}

/* Image Styles */
.allImgHolder {
  text-align: center;
  margin: 0 auto;
}

#profileImgDiv {
  max-width: 700px;
  margin: 0px auto !important;
}

.imgRow {
  margin-bottom: 4%;
}

.imgRow div a img {

  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  border: 1px solid #999;

}

.imgRow div a img:hover {
  -webkit-animation-name: pulse;
  animation-name: pulse;
  -webkit-animation-duration: 1.3s;
  animation-duration: 1.3s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  border: 3px solid #cc9933;
}

span#userFullName {
  display: block;
  padding-bottom: 2%;
  color: #070707;
  font-size: 115%;
  text-decoration: inherit;
}

span#userFullName:hover{
  color: #cc9933;
  text-decoration: inherit;
}

.oddRow {
  text-align: center;
  margin: 0 auto;

}

/* Pulses for profile Images */
@-webkit-keyframes pulse {
  25% {
    -webkit-transform: scale(1.04);
    transform: scale(1.04);
  }

  75% {
    -webkit-transform: scale(0.96);
    transform: scale(0.96);
  }
}

@keyframes pulse {
  15% {
    -webkit-transform: scale(1.12);
    transform: scale(1.12);
  }

  85% {
    -webkit-transform: scale(0.98);
    transform: scale(0.98);
  }
}

/* Paragraph (description) Styles -HomePage */

.topStatement{
  margin: 55px 0 auto;
  text-align: center;
  font-size: 200%;
  color: #070707;
}

.logo{
  color: black;
  font-size:215%;
}

.logoBlack{
  color: #cc9933;
}

.ocLink a:hover {
  color: #cc9933;
}

.ocLink a {
  color: inherit;
}

.nameLink a{
  color: inherit;
}

.descripPar {
  margin: 0px auto;
  margin-bottom: 100px;
  max-width: 840px;
  text-align: center;
  font-size: 240%;
}

/* Footer Styles */
footer {
  max-width: 940px;
  margin: 0 auto;
  text-align: center;
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%);

}

footer p {
  text-align: center;
  font-size: 110%;
  padding: 5px;
  color: #999;
}

footer p a {
