}

footer p a:hover {
  color: #fff;
  text-decoration: none;
}


#socialMediaFooter{
  margin: 8px 0px 2px 0px;
}

#socialMediaFooter a {
  margin: 0 4px;
}

#socialMediaFooter a img:hover {
