/* Slider Styles */
.slider {
  width: 800px;
  height: auto;
  min-height: 500px;
  overflow: hidden;
  /* border: 1px solid black; */
  margin: 0 auto;
}
.sliderNav {
  margin-top: 80px;
  margin-bottom: 16px;
  float: left;
  margin-left: 0;
}
.sliderNav a {
  text-decoration: none;
  font-weight: bold;
}
.sliderNav a:hover {
  color: #cc9933;
  -webkit-transition: all 0.7s ease;
  transition: all 0.7s ease;
}
.slider > ul {
  /* styled by JS to match the added width and height of all <li>’s */
  position: relative;
  -webkit-transition: 0.5s left;
  -moz-transition: 0.5s left;
  -ms-transition: 0.5s left;
  -o-transition: 0.5s left;
  list-style: none;
  margin: 0; padding: 0;
}
.slider > ul > li {
  float: left;
  width: 800px; height: 300px;
}
/* Slider Arrow buttons (Left & Right) */
#leftArrow {
  position: absolute;
  top: 380px;
  left: 0;
  z-index: 5;
  width: 128px;
  height: 128px;
  background-image: url('../graphics/leftArrow.png');
  margin-left: 100px;
  opacity: 0.3;
}
#leftArrow:hover {
  background-image: url('../graphics/leftArrowHov.png');
  opacity: 1.0
}
#rightArrow {
  position: absolute;
  top: 380px;
  right: 0;
  z-index: 5;
  width: 128px;
  height: 128px;
  background-image: url('../graphics/rightArrow.png');
  margin-right: 100px;
  opacity: 0.3;
}
#rightArrow:hover {
  background-image: url('../graphics/rightArrowHov.png');
  opacity: 1.0;
}
.activeSlide {
  color: #cc9933;
}
