@charset "UTF-8";
/* CSS Document

Author: Lucas Mathis
*/
* {
  margin: 0;
  font-family: 'Maven Pro', sans-serif;
  color: #999;
  border-radius: 0; }

/* Main Div Style */
#main {
  max-width: 940px;
  margin: 0 auto;
  background-color: #fff; }

/* Navigation Style */
nav {
  max-width: 940px;
  min-width: 275px;
  margin: 0 auto;
  font-size: 84%;
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%);
  border: 0px solid #fff; }

#navList {
  padding: 0;
  background: #999;
  font-size: 98%;
  margin: 0; }

#navList li a:hover {
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%); }

#navList li {
  margin: 0;
  padding: 0;
  color: #fff;
  border-bottom-style: solid;
  border-color: #000;
  border-width: 1px; }

#navList li a {
  color: #fff; }

.dropdown ul {
  background-color: #ccc; }

.dropdown ul li:hover {
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%); }

/* Fancy Smancy Divider line between images & text */
#indexDivider, hr {
  border: 0;
  height: 2px;
  background-image: -webkit-linear-gradient(left, rgba(204, 204, 204, 0), rgba(204, 204, 204, 0.9), rgba(204, 204, 204, 0));
  background-image: -moz-linear-gradient(left, rgba(204, 204, 204, 0), rgba(204, 204, 204, 0.9), rgba(204, 204, 204, 0));
  background-image: -ms-linear-gradient(left, rgba(204, 204, 204, 0), rgba(204, 204, 204, 0.9), rgba(204, 204, 204, 0));
  background-image: -o-linear-gradient(left, rgba(204, 204, 204, 0), rgba(204, 204, 204, 0.9), rgba(204, 204, 204, 0)); }

/* Image Styles */
.allImgHolder {
  text-align: center;
  margin: 0 auto; }

#profileImgDiv {
  max-width: 700px;
  margin: 0px auto !important; }

.imgRow {
  margin-bottom: 4%; }

.imgRow div a img {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  box-shadow: 0 0 1px transparent;
  border: 1px solid #999; }

.imgRow div a img:hover {
  -webkit-animation-name: pulse;
  animation-name: pulse;
  -webkit-animation-duration: 1.3s;
  animation-duration: 1.3s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  border: 3px solid #cc9933; }

span#userFullName {
  display: block;
  padding-bottom: 2%;
  color: #070707;
  font-size: 115%;
  text-decoration: inherit; }

span#userFullName:hover {
  color: #cc9933;
  text-decoration: inherit; }

.oddRow {
  text-align: center;
  margin: 0 auto; }

/* Pulses for profile Images */
@-webkit-keyframes pulse {
  25% {
    -webkit-transform: scale(1.04);
    transform: scale(1.04); }
  75% {
    -webkit-transform: scale(0.96);
    transform: scale(0.96); } }

@keyframes pulse {
  15% {
    -webkit-transform: scale(1.12);
    transform: scale(1.12); }
  85% {
    -webkit-transform: scale(0.98);
    transform: scale(0.98); } }

/* Paragraph (description) Styles -HomePage */
.topStatement {
  margin: 55px 0 auto;
  text-align: center;
  font-size: 200%;
  color: #070707; }

.logo {
  color: black;
  font-size: 215%; }

.logoBlack {
  color: #cc9933; }

.ocLink a:hover {
  color: #cc9933; }

.ocLink a {
  color: inherit; }

.nameLink a {
  color: inherit; }

.descripPar {
  margin: 0px auto;
  margin-bottom: 100px;
  max-width: 840px;
  text-align: center;
  font-size: 240%; }

/* Footer Styles */
footer {
  max-width: 940px;
  margin: 0 auto;
  text-align: center;
  background: #070707;
  background: -moz-linear-gradient(top, #262626 0%, #070707 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #262626), color-stop(100%, #070707));
  background: -webkit-linear-gradient(top, #262626 0%, #070707 100%);
  background: -o-linear-gradient(top, #262626 0%, #070707 100%);
  background: -ms-linear-gradient(top, #262626 0%, #070707 100%);
  background: linear-gradient(to bottom, #262626 0%, #070707 100%); }

footer p {
  text-align: center;
  font-size: 110%;
  padding: 5px;
  color: #999; }

footer p a {
  color: #999; }

footer p span {
  color: #cc9933; }

footer p a:hover {
  color: #fff;
  text-decoration: none; }

#socialMediaFooter {
  margin: 8px 0px 2px 0px; }

#socialMediaFooter a {
  margin: 0 4px; }

#socialMediaFooter a img:hover {
  border: 1px solid #fff; }

/* Portfolio Page Styles start here
************************************
*/
/* Profile Pic Style */
#portProfilePic {
  margin: 0 auto !important;
  text-align: center; }

#portProfilePic div img {
  width: 260px;
  height: 186px;
  position: relative;
  margin-top: 80px;
  max-width: 940px; }

/* Slider Styles */
.slider {
  width: 800px;
  height: auto;
  min-height: 500px;
  overflow: hidden;
  /* border: 1px solid black; */
  margin: 0 auto; }

.sliderNav {
  margin-top: 80px;
  margin-bottom: 16px;
  float: left;
  margin-left: 0; }

.sliderNav a {
  text-decoration: none;
  font-weight: bold; }

.sliderNav a:hover {
  color: #cc9933;
  -webkit-transition: all 0.7s ease;
  transition: all 0.7s ease; }

.slider > ul {
  /* styled by JS to match the added width and height of all <li>’s */
  position: relative;
  -webkit-transition: 0.5s left;
  -moz-transition: 0.5s left;
  -ms-transition: 0.5s left;
  -o-transition: 0.5s left;
  list-style: none;
  margin: 0;
  padding: 0; }

.slider > ul > li {
  float: left;
  width: 800px;
  height: 300px; }

/* Slider Arrow buttons (Left & Right) */
#leftArrow {
  position: absolute;
  top: 380px;
  left: 0;
  z-index: 5;
  width: 128px;
  height: 128px;
  background-image: url("../graphics/leftArrow.png");
  margin-left: 100px;
  opacity: 0.3; }

#leftArrow:hover {
  background-image: url("../graphics/leftArrowHov.png");
  opacity: 1.0; }

#rightArrow {
  position: absolute;
  top: 380px;
  right: 0;
  z-index: 5;
  width: 128px;
  height: 128px;
  background-image: url("../graphics/rightArrow.png");
  margin-right: 100px;
  opacity: 0.3; }

#rightArrow:hover {
  background-image: url("../graphics/rightArrowHov.png");
  opacity: 1.0; }

.activeSlide {
  color: #cc9933; }

/* Statement Section (in Slider) Styles */
.stmtSlide {
  min-height: 500px;
  max-height: 500px;
  overflow: hidden; }

.stmtPar {
  text-align: center;
  padding: 6% 2%;
  font-size: 260%;
  min-height: 460px;
  overflow: hidden;
  color: #fff; }

#stmtOverlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0; }

/* About Section (in Slider) Styles */
.aboutDiv {
  width: 33%;
  text-align: center;
  float: left; }

.aboutDiv img {
  border: 1px solid black; }

.aboutPar {
  text-align: center;
  font-size: 106%;
  margin: -4px 0 30px 0; }

.bigFont {
  font-size: 130%;
  text-transform: uppercase; }

.aboutColPar {
  font-size: 106%;
  margin: 1% 4%;
  word-wrap: break-word;
  max-width: 260px; }

/* Skill Section (in Slider) Styles */
#skillsSlide p {
  text-align: center;
  margin-top: 4%; }

.skillsDiv {
  width: 33%;
  text-align: center;
  float: left; }

.skillsDiv h3 {
  margin: 10px 0;
  color: #aaa;
  text-decoration: none; }

.skillsDiv p {
  font-size: 110%;
  margin: 0% 6%; }

.iconClass {
