.hrWidth {
  width: 230px;
  text-align: center;
  margin: 0 auto; }

/* My Work Section (in Slider) Styles */
.workLeftDiv {
  width: 60%;
  float: left; }

.workRightDiv {
  width: 40%;
  float: left; }

.thirds img {
  margin: 2%;
