/* Contact Section (in Slider) Styles */
#contactSlide h2 {
  text-align: center;
  margin-top: -2px; }

#contactSlide form {
  margin: 40px 190px; }

#contactSlide form input {
  width: 400px;
  padding: 10px;
  font-size: 110%;
  margin-bottom: 20px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  outline: 0;
  border: 1px solid #aaa; }

#contactSlide form input:focus {
  border: 1px solid #cc9933;
  box-shadow: 0 0 10px #cc9933;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

#contactSlide form textarea:focus {
  border: 1px solid #cc9933;
  box-shadow: 0 0 10px #cc9933;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

#contactSlide form textarea {
  width: 400px;
  padding: 10px;
  font-size: 110%;
  margin-bottom: 20px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  outline: 0;
  border: 1px solid #aaa;
  height: 120px; }

#contactSlide form input[type='submit'] {
  text-align: center;
  margin-top: -26px;
  margin-left: 166px;
  font-size: 110%;
  padding: 10px;
  width: 100px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  border-radius: 6px;
  outline: 0;
  border: 2px solid #cc9933;
  background-color: #fff;
  color: #cc9933;
  box-shadow: none; }

#contactSlide form input[type='submit']:hover {
  background: #000;
  background: -moz-linear-gradient(top, #333333 0%, #141414 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #333333), color-stop(100%, #141414));
  background: -webkit-linear-gradient(top, #333333 0%, #141414 100%);
  background: -o-linear-gradient(top, #333333 0%, #141414 100%);
  background: -ms-linear-gradient(top, #333333 0%, #141414 100%);
  background: linear-gradient(to bottom, #333333 0%, #141414 100%);
  color: #cc9933;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  cursor: pointer;
  box-shadow: none;
  border: 2px solid #cc9933; }

/*********************************/
.memberPhoto {
  margin: 0 50px 10px 130px;
  -webkit-box-shadow: 0 0 1px 1px #CCCCCC;
