/* FAQ Styles */
#faqDiv {
  margin: 4% auto 2% auto;
  max-width: 940px; }

#faqDiv h1 {
  margin: 30px auto 2% auto;
  text-align: center;
  font-size: 340%; }

.panel-heading [data-toggle="collapse"]:after {
  content: "\e072";
  /* "play" icon */
  float: right;
  color: #cc9933;
  font-size: 18px;
  line-height: 22px;
  /* rotate "play" icon from > (right arrow) to down arrow */
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  transform: rotate(-90deg); }

.panel-heading [data-toggle="collapse"].collapsed:after {
  /* rotate "play" icon from > (right arrow) to ^ (up arrow) */
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg);
  color: #cc9933; }

/* FAQ Form Styles */
#faqForm form {
  margin: 2% auto 150px auto; }

#faqForm p {
  text-align: center;
  margin: 0 auto;
  padding: 0 2%;
  font-size: 130%;
  max-width: 940px; }

#faqForm form input {
  width: 90%;
  max-width: 400px;
  padding: 10px;
  font-size: 110%;
  margin-bottom: 20px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  outline: 0;
  border: 1px solid #aaa; }

#faqForm form input:focus {
  border: 1px solid #cc9933;
  box-shadow: 0 0 10px #cc9933;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

#faqForm form textarea:focus {
  border: 1px solid #cc9933;
  box-shadow: 0 0 10px #cc9933;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

#faqForm form textarea {
  width: 90%;
  max-width: 400px;
  padding: 10px;
  font-size: 110%;
  margin-bottom: 20px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  outline: 0;
  border: 1px solid #aaa;
  height: 120px; }

@media screen and (max-width: 480px) {
  #faqForm form textarea {
    resize: none;
    max-width: 90%; }
  #faqDiv h1 {
    font-size: 200%;
    margin: 50px auto 4% auto; } }

#faqForm form input[type='submit'] {
  text-align: center;
  font-size: 110%;
  padding: 10px;
  width: 100px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  border-radius: 6px;
  outline: 0;
  border: 2px solid #cc9933;
  background-color: #fff;
  color: #cc9933;
  box-shadow: none; }

#faqForm form input[type='submit']:hover {
  background: #000;
  background: -moz-linear-gradient(top, #333333 0%, #141414 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #333333), color-stop(100%, #141414));
  background: -webkit-linear-gradient(top, #333333 0%, #141414 100%);
  background: -o-linear-gradient(top, #333333 0%, #141414 100%);
  background: -ms-linear-gradient(top, #333333 0%, #141414 100%);
  background: linear-gradient(to bottom, #333333 0%, #141414 100%);
  color: #cc9933;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  cursor: pointer;
  box-shadow: none;
  border: 2px solid #cc9933; }

#contact {
  text-align: center;
  margin: 0 auto; }

.contactReturn {
